<template>
  <div class="note-sidebar">
    <span class="btn add-note" @click="onAddNote"></span>
    <span v-if="!curBook.id" class="notebook-title"></span>
    <el-dropdown
      v-if="curBook.id"
      class="notebook-title"
      @command="handleCommand"
      placement="bottom"
    >
      <span class="el-dropdown-link"
        >{{ curBook.title }} <i class="iconfont icon-down"></i
      ></span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
          v-for="notebook in notebooks"
          :command="notebook.id"
          :key="notebook.id"
        >
          {{ notebook.title }}
        </el-dropdown-item>
        <el-dropdown-item command="trash"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <div class="menu">
      <div></div>
      <div></div>
    </div>
    <ul class="notes">
      <li v-for="note in notes" :key="note.id">
        <router-link :to="`/note?noteId=${note.id}&notebook=${curBook.id}`">
          <span class="date">{{ note.updatedAtFriendly }}</span>
          <span class="title">{{ note.title }}</span></router-link
        >
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";

export default {
  props: ["curNote"],
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["notebooks", "notes", "curBook", "curNote"])
  },
  created() {
    // 
    this.getNotebooks()
      .then(() => {
        // ID
        this.setCurBook({ curBookId: this.$route.query.notebookId });
        // 
        if (this.curBook.id) {
          return this.getNotes({ notebookId: this.curBook.id });
        }
      })
      .then(() => {
        // Id,getter id 
        this.setCurNote({ curNoteId: this.$route.query.noteId });
        //  url 
        this.$router.replace({
          path: "/note",
          query: {
            noteId: this.curNote.id,
            notebookId: this.curBook.id
          }
        });
      });
  },
  methods: {
    ...mapMutations(["setCurBook", "setCurNote"]),
    ...mapActions(["getNotebooks", "getNotes", "addNote"]),

    // 
    // notebookId   Key
    handleCommand(notebookId) {
      if (notebookId == "trash") {
        return this.$router.push("/trash");
      }
      // id
      this.setCurBook({ curBookId: notebookId });
      // Id 
      this.getNotes({ notebookId }).then(() => {
        //  url 
        this.setCurNote();
        this.$router.replace({
          path: "/note",
          query: {
            noteId: this.curNote.id,
            notebookId: this.curBook.id
          }
        });
      });
    },

    // 
    onAddNote() {
      this.addNote({ notebookId: this.curBook.id });
    }
  }
};
</script>

<style lang="less">
@import url("../assets/css/note-sidebar.less");
</style>
