<template>
  <div id="note">
    <!--  -->
    <NoteSidebar
      @update:notes="(val) => (notes = val)"
      :curNote="curNote"
    ></NoteSidebar>
    <div class="note-detail">
      <div class="note-empty" v-show="!curBook.id"></div>
      <div class="note-empty" v-show="!curNote.id"></div>
      <div class="note-detail-ct" v-show="curNote.id">
        <div class="note-bar">
          <span> : {{ curNote.createdAtFriendly }}</span>
          <span> : {{ curNote.updatedAtFriendly }}</span>
          <span> {{ statusText }}</span>
          <span class="iconfont icon-delete" @click="onDeleteNote"></span>
          <span
            class="iconfont icon-fullscreen"
            @click="isShowPreview = !isShowPreview"
          ></span>
        </div>
        <div class="note-title">
          <input
            type="text"
            v-model="curNote.title"
            @input="onUpdateNote"
            @keydown="statusText = '...'"
            placeholder=""
          />
        </div>
        <div class="editor">
          <codemirror
            v-model="curNote.content"
            :options="cmOptions"
            v-show="!isShowPreview"
            @input="ononUpdateNote"
            @inputRead="statusText = '...'"
          ></codemirror>
          <!-- <textarea
            v-show="isShowPreview"
            v-model="curNote.content"
            @input="onUpdateNote"
            @keydown="statusText = '...'"
            placeholder=",  markdown "
          ></textarea> -->
          <div
            class="preview markdown-body"
            v-html="previewContent"
            v-show="!isShowPreview"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NoteSidebar from "./NoteSidebar";
import _ from "lodash";
import MarkdownIt from "markdown-it";
import { mapGetters, mapMutations, mapActions } from "vuex";
import { codemirror } from "vue-codemirror";
import "codemirror/lib/codemirror.css";
import "codemirror/mode/markdown/markdown.js";
import "codemirror/theme/neat.css";

let md = new MarkdownIt();

export default {
  name: "NoteDetail",
  components: { NoteSidebar, codemirror },
  data() {
    return {
      isShowPreview: false,
      statusText: "",
      cmOptions: {
        tabSize: 4,
        mode: "text/x-markdown",
        theme: "neat",
        lineNumbers: false,
        line: true
      }
    };
  },
  computed: {
    ...mapGetters(["notes", "curNote", "curBook"]),

    //  md 
    // md.render  md  html 
    previewContent() {
      return md.render(this.curNote.content || "");
    }
  },

  created() {
    this.checkLogin({ path: "/login" });
  },

  // ID
  beforeRouteUpdate(to, next) {
    this.setCurNote({ curNoteId: to.query.noteId });
    next();
  },

  methods: {
    ...mapMutations(["setCurNote"]),

    ...mapActions(["updateNote", "deleteNote", "checkLogin"]),

    // 
    // 
    onUpdateNote: _.debounce(function() {
      this.updateNote({
        noteId: this.curNote.id,
        title: this.curNote.title,
        content: this.curNote.content
      })
        .then(() => {
          this.statusText = "";
        })
        .catch(() => {
          this.statusText = "";
        });
    }, 300),

    // 
    onDeleteNote() {
      this.deleteNote({ noteId: this.curNote.id }).then(() => {
        this.$router.replace({ path: "/note" });
      });
    }
  }
};
</script>

<style scoped lang="less">
@import url(../assets/css/note-detail.less);
#note {
  display: flex;
  align-items: stretch;
  background: #fff;
  flex: 1;
}
</style>
